<template>
    <div class="job-profile-center">
        <div class="title">职位画像分析</div>
        
        <!-- 3D旋转星空背景 -->
        <div class="starfield-bg">
            <canvas ref="starCanvas"></canvas>
        </div>
        
        <div class="profile-content">
            <!-- 职位画像中心SVG -->
            <div class="profile-svg-container">
                <svg class="profile-svg" xmlns="http://www.w3.org/2000/svg" version="1.1" viewBox="0 0 600 500">
                    <!-- 背景轨道动画 -->
                    <g class="orbit-group">
                        <circle cx="300" cy="250" r="180" fill="none" stroke="rgba(44, 168, 254, 0.1)" stroke-width="1" stroke-dasharray="5,5">
                            <animateTransform attributeName="transform" type="rotate" from="0 300 250" to="360 300 250" dur="20s" repeatCount="indefinite" />
                        </circle>
                        <circle cx="300" cy="250" r="140" fill="none" stroke="rgba(44, 168, 254, 0.1)" stroke-width="1" stroke-dasharray="3,3">
                            <animateTransform attributeName="transform" type="rotate" from="360 300 250" to="0 300 250" dur="30s" repeatCount="indefinite" />
                        </circle>
                        <circle cx="300" cy="250" r="100" fill="none" stroke="rgba(44, 168, 254, 0.1)" stroke-width="1" stroke-dasharray="2,2">
                            <animateTransform attributeName="transform" type="rotate" from="0 300 250" to="360 300 250" dur="15s" repeatCount="indefinite" />
                        </circle>
                    </g>
                    
                    <!-- 中心核心 -->
                    <g class="core-group">
                        <!-- 脉冲效果 -->
                        <circle cx="300" cy="250" r="0" fill="none" stroke="#2ca8fe" stroke-width="3" stroke-opacity="0">
                            <animate attributeName="r" from="0" to="80" dur="2s" repeatCount="indefinite" />
                            <animate attributeName="stroke-opacity" from="1" to="0" dur="2s" repeatCount="indefinite" />
                        </circle>
                        
                        <!-- 中心圆形 -->
                        <circle cx="300" cy="250" r="60" fill="url(#centerGradient)" stroke="#2ca8fe" stroke-width="2" class="center-circle" />
                        
                        <!-- 中心发光效果 -->
                        <circle cx="300" cy="250" r="65" fill="none" stroke="url(#glowGradient)" stroke-width="2" opacity="0.8">
                            <animate attributeName="stroke-width" from="2" to="6" dur="1.5s" repeatCount="indefinite" />
                            <animate attributeName="opacity" from="0.8" to="0.3" dur="1.5s" repeatCount="indefinite" />
                        </circle>
                    </g>
                    
                    <!-- 连接线和外部节点 -->
                    <g v-for="(profile, index) in visibleProfiles" :key="index"
                       :class="{ 'profile-active': isActiveProfile(index) }">
                        <!-- 计算放射状布局的位置 -->
                        <g class="connection-group">
                            <!-- 连接线 -->
                            <line :x1="300" :y1="250" 
                                  :x2="getNodeX(index)" 
                                  :y2="getNodeY(index)"
                                  stroke="url(#lineGradient)" stroke-width="2" opacity="0.6" />
                            
                            <!-- 粒子流动效果 -->
                            <circle r="2" fill="#feed2c">
                                <animateMotion dur="3s" repeatCount="indefinite">
                                    <mpath :href="`#path${index}`" />
                                </animateMotion>
                            </circle>
                            
                            <!-- 路径定义（用于粒子动画） -->
                            <path :id="`path${index}`" :d="`M300,250 L${getNodeX(index)},${getNodeY(index)}`" fill="none" />
                        </g>
                        
                        <!-- 节点组 -->
                        <g class="node-group" @click="selectProfile(index + currentPage * pageSize)"
                           @mouseenter="hoveredNode = index" @mouseleave="hoveredNode = -1">
                            <!-- 节点背景光晕 -->
                            <circle :cx="getNodeX(index)" 
                                    :cy="getNodeY(index)"
                                    r="35" fill="url(#nodeGlowGradient)" opacity="0.3"
                                    :class="{ 'node-glow-active': hoveredNode === index }" />
                            
                            <!-- 节点圆 -->
                            <circle :cx="getNodeX(index)" 
                                    :cy="getNodeY(index)"
                                    r="30" fill="rgba(3, 4, 74, 0.9)" stroke="#2ca8fe" stroke-width="1.5"
                                    class="node-circle" />
                            
                            <!-- 节点内圈 -->
                            <circle :cx="getNodeX(index)" 
                                    :cy="getNodeY(index)"
                                    r="25" fill="none" stroke="rgba(44, 168, 254, 0.3)" stroke-width="1"
                                    stroke-dasharray="2,2">
                                <animateTransform attributeName="transform" 
                                    :values="`0 ${getNodeX(index)} ${getNodeY(index)};360 ${getNodeX(index)} ${getNodeY(index)}`"
                                    dur="10s" repeatCount="indefinite" />
                            </circle>
                        </g>
                        
                        <!-- 职位名称 -->
                        <foreignObject 
                            :x="getNodeX(index) - 40" 
                            :y="getNodeY(index) - 15"
                            width="80" height="30">
                            <div xmlns="http://www.w3.org/1999/xhtml" class="profile-node-text"
                                 :class="{ 'text-active': isActiveProfile(index) }">
                                {{ truncateTitle(profile.title) }}
                            </div>
                        </foreignObject>
                    </g>
                    
                    <!-- SVG定义 -->
                    <defs>
                        <radialGradient id="centerGradient">
                            <stop offset="0%" stop-color="#75deef" stop-opacity="0.8" />
                            <stop offset="100%" stop-color="#2ca8fe" stop-opacity="0.6" />
                        </radialGradient>
                        
                        <radialGradient id="glowGradient">
                            <stop offset="0%" stop-color="#feed2c" stop-opacity="0.8" />
                            <stop offset="100%" stop-color="#feed2c" stop-opacity="0.1" />
                        </radialGradient>
                        
                        <linearGradient id="lineGradient" x1="0%" y1="0%" x2="100%" y2="100%">
                            <stop offset="0%" stop-color="#2ca8fe" stop-opacity="0.8" />
                            <stop offset="100%" stop-color="#75deef" stop-opacity="0.3" />
                        </linearGradient>
                        
                        <radialGradient id="nodeGlowGradient">
                            <stop offset="0%" stop-color="#feed2c" stop-opacity="0.6" />
                            <stop offset="100%" stop-color="#feed2c" stop-opacity="0" />
                        </radialGradient>
                    </defs>
                </svg>
                
                <!-- 中心职位名称显示 -->
                <div class="center-title">
                    <div class="center-title-text">{{ activeProfile ? truncatedActiveTitle : '职位画像' }}</div>
                    <div class="center-title-sub">{{ activeProfile ? activeProfile.count + ' 个职位' : '' }}</div>
                </div>
            </div>
            
            <!-- 职位详情（3D卡片效果） -->
            <div v-if="activeProfile" class="active-profile">
                <div class="profile-card">
                    <div class="card-header">
                        <div class="card-title">{{ truncatedActiveTitle }}</div>
                        <div class="card-count">
                            <span class="count-number">{{ activeProfile.count }}</span>
                            <span class="count-label">招聘职位</span>
                        </div>
                    </div>
                    
                    <div class="card-content">
                        <div class="detail-item salary-item">
                            <div class="detail-icon">💰</div>
                            <div class="detail-info">
                                <div class="detail-label">平均薪资</div>
                                <div class="detail-value">{{ activeProfile.avgSalary }}</div>
                            </div>
                        </div>
                        
                        <div class="detail-item">
                            <div class="detail-icon">🏙️</div>
                            <div class="detail-info">
                                <div class="detail-label">热门城市</div>
                                <div class="detail-value">{{ formatTopItems(activeProfile.topCities) }}</div>
                            </div>
                        </div>
                        
                        <div class="detail-item">
                            <div class="detail-icon">🏢</div>
                            <div class="detail-info">
                                <div class="detail-label">主要行业</div>
                                <div class="detail-value">{{ formatTopItems(activeProfile.topIndustries) }}</div>
                            </div>
                        </div>
                        
                        <div class="detail-item">
                            <div class="detail-icon">🎓</div>
                            <div class="detail-info">
                                <div class="detail-label">经验要求</div>
                                <div class="detail-value">{{ formatTopItems(activeProfile.topExperiences) }}</div>
                            </div>
                        </div>
                        
                        <div class="detail-item">
                            <div class="detail-icon">📚</div>
                            <div class="detail-info">
                                <div class="detail-label">学历要求</div>
                                <div class="detail-value">{{ formatTopItems(activeProfile.topEducations) }}</div>
                            </div>
                        </div>
                    </div>
                </div>
                
                <!-- 翻页控制器 -->
                <div v-if="profiles.length > pageSize" class="profile-nav">
                    <div class="nav-button" @click="prevPage" :class="{disabled: currentPage === 0}">
                        <span class="nav-arrow">◀</span>
                    </div>
                    <div class="page-indicators">
                        <span v-for="page in totalPages" :key="page"
                              class="page-dot" 
                              :class="{ active: currentPage === page - 1 }"
                              @click="currentPage = page - 1"></span>
                    </div>
                    <div class="nav-button" @click="nextPage" :class="{disabled: currentPage >= totalPages - 1}">
                        <span class="nav-arrow">▶</span>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'JobProfileCenter',
    props: {
        profiles: {
            type: Array,
            default: () => []
        }
    },
    data() {
        return {
            activeProfileIndex: 0,
            autoSwitchInterval: null,
            currentPage: 0,
            pageSize: 6,  // 每页显示6个节点，适合放射状布局
            animationFrameId: null,
            rotationAngle: 0,
            hoveredNode: -1,
            starFieldCtx: null
        }
    },
    computed: {
        activeProfile() {
            return this.profiles && this.profiles.length > 0 
                ? this.profiles[this.activeProfileIndex] 
                : null;
        },
        totalPages() {
            return Math.ceil(this.profiles.length / this.pageSize);
        },
        visibleProfiles() {
            const start = this.currentPage * this.pageSize;
            return this.profiles.slice(start, start + this.pageSize);
        },
        // 添加截取标题的计算属性
        truncatedActiveTitle() {
            if (!this.activeProfile || !this.activeProfile.title) return '';
            return this.activeProfile.title.substring(0, 10);
        }
    },
    methods: {
        // 添加一个通用方法用于截取标题
        truncateTitle(title) {
            if (!title) return '';
            return title.length > 10 ? title.substring(0, 10) : title;
        },
        
        setChart() {
            // 初始化
            if (this.profiles && this.profiles.length > 0) {
                this.startAutoSwitch();
                this.initStarfield();
            }
        },
        
        initStarfield() {
            const canvas = this.$refs.starCanvas;
            if (!canvas) return;
            
            const ctx = canvas.getContext('2d');
            const width = canvas.width = canvas.offsetWidth;
            const height = canvas.height = canvas.offsetHeight;
            
            const stars = [];
            const starCount = 200;
            
            // 创建星星
            for (let i = 0; i < starCount; i++) {
                stars.push({
                    x: Math.random() * width,
                    y: Math.random() * height,
                    radius: Math.random() * 1.5,
                    vx: (Math.random() - 0.5) * 0.1,
                    vy: (Math.random() - 0.5) * 0.1,
                    opacity: Math.random()
                });
            }
            
            const animate = () => {
                ctx.clearRect(0, 0, width, height);
                
                stars.forEach(star => {
                    ctx.beginPath();
                    ctx.arc(star.x, star.y, star.radius, 0, Math.PI * 2);
                    ctx.fillStyle = `rgba(117, 222, 239, ${star.opacity})`;
                    ctx.fill();
                    
                    // 移动星星
                    star.x += star.vx;
                    star.y += star.vy;
                    
                    // 边界处理
                    if (star.x < 0 || star.x > width) star.vx = -star.vx;
                    if (star.y < 0 || star.y > height) star.vy = -star.vy;
                    
                    // 闪烁效果
                    star.opacity += (Math.random() - 0.5) * 0.05;
                    star.opacity = Math.max(0.1, Math.min(1, star.opacity));
                });
                
                requestAnimationFrame(animate);
            };
            
            animate();
        },
        
        getNodeX(index) {
            const angle = (2 * Math.PI * index / this.visibleProfiles.length) - Math.PI/2;
            return 300 + 180 * Math.cos(angle);
        },
        
        getNodeY(index) {
            const angle = (2 * Math.PI * index / this.visibleProfiles.length) - Math.PI/2;
            return 250 + 180 * Math.sin(angle);
        },
        
        isActiveProfile(index) {
            return this.activeProfileIndex === (index + this.currentPage * this.pageSize);
        },
        
        selectProfile(index) {
            if (index < this.profiles.length) {
                this.activeProfileIndex = index;
                // 重置自动切换定时器
                this.startAutoSwitch();
            }
        },
        
        prevPage() {
            if (this.currentPage > 0) {
                this.currentPage--;
                // 选择当前页的第一个职位
                this.activeProfileIndex = this.currentPage * this.pageSize;
            }
        },
        
        nextPage() {
            if (this.currentPage < this.totalPages - 1) {
                this.currentPage++;
                // 选择当前页的第一个职位
                this.activeProfileIndex = this.currentPage * this.pageSize;
            }
        },
        
        startAutoSwitch() {
            // 清除之前的定时器
            if (this.autoSwitchInterval) {
                clearInterval(this.autoSwitchInterval);
            }
            
            // 设置新的定时器，每8秒切换一次
            this.autoSwitchInterval = setInterval(() => {
                if (this.profiles && this.profiles.length > 0) {
                    // 切换到下一个职位
                    this.activeProfileIndex = (this.activeProfileIndex + 1) % this.profiles.length;
                    
                    // 如果当前显示的页面不包含活动职位，切换到对应页面
                    const targetPage = Math.floor(this.activeProfileIndex / this.pageSize);
                    if (targetPage !== this.currentPage) {
                        this.currentPage = targetPage;
                    }
                }
            }, 8000);
        },
        
        formatTopItems(items) {
            if (!items || items.length === 0) return '暂无数据';
            
            // 限制显示的城市数量，避免内容过多
            // 对于城市，只显示前2个
            if (items[0] && items[0].city) {
                items = items.slice(0, 2);
            }
            
            return items.map(item => {
                // 根据不同的项目类型，选择正确的属性名称
                const name = item.city || item.industry || item.exp || item.edu;
                return `${name}(${item.count})`;
            }).join('、');
        }
    },
    mounted() {
        this.setChart();
    },
    beforeDestroy() {
        if (this.autoSwitchInterval) {
            clearInterval(this.autoSwitchInterval);
        }
        
        if (this.animationFrameId) {
            cancelAnimationFrame(this.animationFrameId);
        }
    }
}
</script>

<style lang="less" scoped>
.job-profile-center {
    width: 100%;
    height: 100%;
    padding: 0;
    position: relative;
    overflow: hidden;
    background: rgba(0, 0, 30, 0.3);
    
    .title {
        height: 24px;
        line-height: 24px;
        font-size: 14px;
        color: #75deef;
        padding: 0 10px;
        position: relative;
        z-index: 10;
        text-shadow: 0 0 10px rgba(117, 222, 239, 0.8);
        animation: title-glow 2s infinite alternate;
        top: 20px;
        
        &:before {
            content: '';
            position: absolute;
            left: 2px;
            top: 50%;
            transform: translateY(-50%);
            width: 3px;
            height: 14px;
            background: linear-gradient(to bottom, #75deef, #2ca8fe);
            box-shadow: 0 0 8px rgba(117, 222, 239, 0.8);
        }
    }
    
    .starfield-bg {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        opacity: 0.3;
        z-index: 1;
        
        canvas {
            width: 100%;
            height: 100%;
        }
    }
    
    .profile-content {
        display: flex;
        height: calc(100% - 24px);
        position: relative;
        z-index: 5;
        
        .profile-svg-container {
            flex: 2;
            padding: 5px;
            position: relative;
            
            .profile-svg {
                width: 100%;
                height: 100%;
                
                .orbit-group {
                    opacity: 0.6;
                }
                
                .center-circle {
                    cursor: pointer;
                    filter: drop-shadow(0 0 20px rgba(44, 168, 254, 0.8));
                    animation: center-pulse 3s infinite;
                }
                
                .connection-group {
                    line {
                        filter: drop-shadow(0 0 5px rgba(44, 168, 254, 0.6));
                    }
                }
                
                .node-group {
                    cursor: pointer;
                    
                    .node-circle {
                        transition: all 0.3s ease;
                        filter: drop-shadow(0 0 10px rgba(44, 168, 254, 0.5));
                    }
                    
                    .node-glow-active {
                        animation: node-glow 1s infinite;
                    }
                    
                    &:hover .node-circle {
                        fill: rgba(44, 168, 254, 0.2);
                        stroke-width: 2;
                        filter: drop-shadow(0 0 15px rgba(254, 237, 44, 0.8));
                    }
                }
                
                .profile-node-text {
                    color: #75deef;
                    font-size: 12px;
                    text-align: center;
                    cursor: pointer;
                    user-select: none;
                    transition: all 0.3s ease;
                    
                    &.text-active {
                        color: #feed2c;
                        font-weight: bold;
                        text-shadow: 0 0 8px rgba(254, 237, 44, 0.8);
                    }
                    
                    &:hover {
                        color: #feed2c;
                        transform: scale(1.1);
                    }
                }
                
                g.profile-active {
                    .connection-group line {
                        stroke: url(#activeLineGradient);
                        stroke-width: 3;
                        filter: drop-shadow(0 0 10px rgba(254, 237, 44, 0.8));
                    }
                    
                    .node-circle {
                        fill: rgba(254, 237, 44, 0.2);
                        stroke: #feed2c;
                        stroke-width: 2;
                    }
                }
            }
            
            .center-title {
                position: absolute;
                top: 50%;
                left: 50%;
                
                transform: translate(-50%, -50%);
                text-align: center;
                z-index: 10;
                pointer-events: none;
                
                .center-title-text {
                    color: #feed2c;
                    font-size: 16px;
                    font-weight: bold;
                    text-shadow: 0 0 10px rgba(254, 237, 44, 0.8);
                    animation: text-pulse 2s infinite;
                }
                
                .center-title-sub {
                    color: #75deef;
                    font-size: 12px;
                    margin-top: 2px;
                }
            }
        }
        
        .active-profile {
            flex: 1;
            padding: 10px;
            display: flex;
            flex-direction: column;
            perspective: 1000px;
            
            .profile-card {
                background: rgba(3, 4, 74, 0.9);
                border: 1px solid rgba(44, 168, 254, 0.6);
                border-radius: 8px;
                box-shadow: 0 0 20px rgba(13, 36, 81, 0.5);
                transform: rotateY(0deg);
                transform-style: preserve-3d;
                transition: transform 0.6s;
                position: relative;
                overflow: hidden;
                
                &:before {
                    content: '';
                    position: absolute;
                    top: 0;
                    left: -100%;
                    width: 100%;
                    height: 100%;
                    background: linear-gradient(90deg, transparent, rgba(44, 168, 254, 0.3), transparent);
                    transition: left 0.6s;
                }
                
                &:hover {
                    transform: rotateY(3deg);
                    
                    &:before {
                        left: 100%;
                    }
                }
                
                .card-header {
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    padding: 12px;
                    border-bottom: 1px solid rgba(44, 168, 254, 0.3);
                    background: rgba(44, 168, 254, 0.1);
                    
                    .card-title {
                        font-size: 18px;
                        color: #75deef;
                        font-weight: bold;
                        text-shadow: 0 0 8px rgba(117, 222, 239, 0.8);
                    }
                    
                    .card-count {
                        display: flex;
                        flex-direction: column;
                        align-items: center;
                        background: rgba(254, 237, 44, 0.1);
                        padding: 5px 10px;
                        border-radius: 20px;
                        border: 1px solid rgba(254, 237, 44, 0.3);
                        
                        .count-number {
                            font-size: 20px;
                            color: #feed2c;
                            font-weight: bold;
                            text-shadow: 0 0 5px rgba(254, 237, 44, 0.8);
                        }
                        
                        .count-label {
                            font-size: 12px;
                            color: #75deef;
                        }
                    }
                }
                
                .card-content {
                    padding: 12px;
                    
                    .detail-item {
                        display: flex;
                        align-items: center;
                        padding: 8px;
                        margin-bottom: 8px;
                        background: rgba(3, 4, 74, 0.5);
                        border-radius: 5px;
                        transition: all 0.3s ease;
                        border: 1px solid transparent;
                        
                        &:hover {
                            background: rgba(44, 168, 254, 0.1);
                            border-color: rgba(44, 168, 254, 0.3);
                            transform: translateX(5px);
                        }
                        
                        &.salary-item {
                            background: rgba(254, 237, 44, 0.1);
                            border: 1px solid rgba(254, 237, 44, 0.3);
                            
                            .detail-value {
                                color: #feed2c;
                                font-size: 18px;
                                font-weight: bold;
                            }
                        }
                        
                        .detail-icon {
                            font-size: 24px;
                            margin-right: 10px;
                            filter: drop-shadow(0 0 5px rgba(255, 255, 255, 0.5));
                        }
                        
                        .detail-info {
                            flex: 1;
                            
                            .detail-label {
                                color: #75deef;
                                font-size: 12px;
                                margin-bottom: 2px;
                            }
                            
                            .detail-value {
                                color: #fff;
                                font-size: 14px;
                                line-height: 1.4;
                            }
                        }
                    }
                }
            }
            
            .profile-nav {
                display: flex;
                justify-content: center;
                align-items: center;
                margin-top: 10px;
                gap: 15px;
                
                .nav-button {
                    width: 40px;
                    height: 40px;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    background: rgba(44, 168, 254, 0.1);
                    border: 1px solid rgba(44, 168, 254, 0.3);
                    border-radius: 20px;
                    cursor: pointer;
                    transition: all 0.3s ease;
                    
                    &:hover:not(.disabled) {
                        background: rgba(44, 168, 254, 0.3);
                        transform: scale(1.1);
                        box-shadow: 0 0 15px rgba(44, 168, 254, 0.5);
                    }
                    
                    &.disabled {
                        opacity: 0.3;
                        cursor: not-allowed;
                    }
                    
                    .nav-arrow {
                        color: #75deef;
                        font-size: 18px;
                        font-weight: bold;
                    }
                }
                
                .page-indicators {
                    display: flex;
                    gap: 8px;
                    
                    .page-dot {
                        width: 8px;
                        height: 8px;
                        border-radius: 50%;
                        background: rgba(117, 222, 239, 0.3);
                        cursor: pointer;
                        transition: all 0.3s ease;
                        
                        &.active {
                            background: #feed2c;
                            box-shadow: 0 0 10px rgba(254, 237, 44, 0.8);
                            transform: scale(1.3);
                        }
                        
                        &:hover:not(.active) {
                            background: rgba(117, 222, 239, 0.6);
                        }
                    }
                }
            }
        }
    }
}

@keyframes title-glow {
